import { useNavigate } from "react-router-dom";
import { Avatar, ButtonGroup, colors, ExtraText, StyledButton, StyledFormAread, StyledTitle } from "../components/Styles";
import Logo from "./../assets/logo.png";
import { confirmEmail } from "../auth/actions/userAction";
import { useSearchParams } from "react-router-dom"
import { connect } from "react-redux";

const ConfirmationEmail = ({confirmEmail}) => {
    const [searchParams, setSearchParams] = useSearchParams();
    const confirmation_code = searchParams.get("confirmation_code");
    confirmEmail(confirmation_code)
    return (
        <div>
            <div
                style={{
                    position: "absolute",
                    top: 0,
                    left: 0,
                    backgroundColor: "transparent",
                    width: "100%",
                    padding: "15px",
                    display: "flex",
                    justifyContent: "flex-start"
                }}
            >
                <Avatar image={Logo} />
            </div>
                <StyledFormAread bg={colors.dark2}>
                <StyledTitle size={65}>
                    Account Confirmation
                </StyledTitle>
                <ExtraText color={colors.light1}>
                    Welcome to Altimate ai !
                </ExtraText>
                <ExtraText color={colors.light1}>
                    Your email is validated ! You can Proceed new_password
                </ExtraText>
                <ButtonGroup>
                    <StyledButton to="/login">
                        Proceed
                    </StyledButton>
                </ButtonGroup>
                </StyledFormAread>      
        </div>
    )
}

export default connect(null, {confirmEmail})(ConfirmationEmail);